<template>
    <div class="weui_tabbar">
        <a v-for="tabbar in tabbars" 
            class="weui_tabbar_item"
            :class="[tabbar.name, {weui_bar_item_on: activeTabbar.name == tabbar.name }]"
            @click="updateActiveTabbar(tabbar)">
            <div class="weui_tabbar_icon" @click="indexVibrate">
                <span></span>
            </div>
            <p class="weui_tabbar_label">{{tabbar.text}}</p>
        </a>
    </div>
</template>

<script>

import { updateActiveTabbar } from 'actions'

export default {
  name: 'Tabbar',
  vuex: {
    getters: {
        activeTabbar: state => state.activeTabbar
    },
    actions: {
        updateActiveTabbar
    }
  },
  props:{
    tabbars: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  methods: {
    indexVibrate(){
      navigator.vibrate(50)
    }
  }
}
</script>

<style lang="less">

@import "../common/style/widget/weui_tab/tabbar";

.weui_tabbar_icon span {
    display: block;
    width: 100%;
    height: 100%;
}

</style>


<style lang="less" scoped>

.Server .weui_tabbar_icon span {
    background-image: url("../home/img/server.png");
}
.Server.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/server_active.png");
}

.Driving .weui_tabbar_icon span {
    background-image: url("../home/img/driving.png");
}
.Driving.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/driving_active.png");
}

.Home .weui_tabbar_icon span {
    background-image: url("../home/img/home.png");
}
.Home.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/home_active.png");
}

.Test .weui_tabbar_icon span {
    background-image: url("../home/img/test.png");
}
.Test.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/test_active.png");
}

.Myself .weui_tabbar_icon span {
    background-image: url("../home/img/myself.png");
}
.Myself.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/myself_active.png");
}

.Control .weui_tabbar_icon span {
    background-image: url("../home/img/control.png");
}
.Control.weui_bar_item_on .weui_tabbar_icon span {
    background-image: url("../home/img/control_active.png");
}
.weui_tabbar_icon {
    width: 31px;
    height: 24px;
}
.weui_tabbar_icon span {
  background-size: 31px 24px;
}
.weui_tabbar {
  background-color: #0b0e10;
}
.weui_tabbar_item.weui_bar_item_on .weui_tabbar_label {
  color: #01c3fc;
}
.weui_tabbar_label {
  color: rgba(250, 251, 250, 0.77);
}
</style>



