<template>
  <div class="vux-circle">
    <svg viewBox="0 0 100 100">
      <path :d="pathString" stroke-linecap="round" :stroke="strokeColor" :stroke-width="strokeWidth" fill-opacity="0" :style="pathStyle" />
    </svg>
    <div class="vux-circle-content"><slot></slot></div>
  </div>
</template>

<script>

export default {
  props: {
    strokeWidth: {
      type: Number,
      default: 1
    },
    strokeColor: {
      type: String,
      default: '#3FC7FA'
    },
    duration: {
      type: Number,
      default: 0.6
    },
    direction: {
      type: Number,
      default: 1
    },
    size: {
      type: Number,
      default: 1
    },
    percent: {
      type: Number,
      default: 0
    }
  },
  computed: {
    _direction (){
      return this.direction ? 1 : 0
    },
    radius () {
      return 50 - this.strokeWidth / 2
    },
    pathString () {
      return `M 50,50 m 0,-${this.radius}
      a ${this.radius},${this.radius} 0 1 ${this._direction} 0,${2 * this.radius}
      a ${this.radius},${this.radius} 0 1 ${this._direction} 0,-${2 * this.radius}`
    },
    len () {
      return Math.PI * 2 * this.radius
    },
    pathStyle () {
      return {
        'stroke-dasharray': `${this.len}px ${this.len}px`,
        'stroke-dashoffset': `${((100 - this.percent*this.size) / 100 * this.len)}px`,
        'transition': `stroke-dashoffset ${this.duration}s ease 0s, stroke ${this.duration}s ease`
      }
    }
  }
}
</script>

<style>
.vux-circle {
  position: relative;
  width: 100%;
  height: 100%;
}
.vux-circle-content {
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
</style>



