import profile from '../profile/profile.js'
import Fetch from 'plugin/fetch/index'
// import { assign } from '../../plugin/fetch/util';

export var basePath = profile.serverUrl

const login_URL = basePath + "/api/user/login";

export function login(){
	console.log(login_URL)
	return Fetch(login_URL, {
		isLogin: true,
		method: 'POST',
		body: {
			phone: 15215167541,
            password: 'Li135454',
		}
	})
}

export function $Fetch (url, options = {} ){
	return Fetch(url, options).then(function(res){
		if (!options.isLogin && !res.success && res.code == "2") {
			console.log('');
			login().then( res => {
				options.body.token = res.data.token;
				$Fetch(url, options)
			})
		}
	});
}

['get', 'delete', 'head'].forEach((method) => {

    $Fetch[method] = function (url, options) {
        return this(url, assign(options || {}, {method}));
    };

});

['post', 'put', 'patch'].forEach((method) => {

    $Fetch[method] = function (url, body, options) {
        return this(url, assign(options || {}, {url, method, body}));
    };

});


