'use strict'

import Vue from 'vue'

import Router from 'vue-router'

import App from './App.vue'
import Hello from './home/IndexView.vue'
import ButtonView from './home/ButtonView.vue'
import ActionSheetView from './home/ActionSheetView.vue'
import CellView from './home/CellView.vue'


// fetch(__DEVAPI__+'http://localhost:8888')
//   .then(function(response) {
//     return response.text()
//   }).then(function(body) {
//     document.body.innerHTML = body
//   })


var Routerlink = [],
    currentIndex = 0

// install router
Vue.use(Router)


// register filters globally

// routing
var router = new Router()

router.map({
  '/': {
    name: 'index',  // 
    component: Hello
  },
  '/button': {
    component: ButtonView
  },
  // '/toast': {
  //   component: ToastView
  // },
  // '/dialog': {
  //   component: DialogView
  // },
  '/actionsheet': {
    component: ActionSheetView
  },
  '/cell': {
    component: function(resolve){
      require(['./home/CellView.vue'],resolve)
    }
  },
  // '/cell': {
  //   component: CellView
  // }
})

router.redirect({
  '*': '/'
})

var setRouterLink = function(transition){
  if(!hasRouterLink(transition)){
    Routerlink.push(transition.to)
  } else {
    Routerlink = Routerlink.slice(0,currentIndex+1)
  }
}

var hasRouterLink = function(transition){
  return Routerlink.filter( (item,index) => {
    if(item.path == transition.to.path){
      currentIndex = index
      return true
    }
    return false
  })[0]
}

router.beforeEach(function (transition) {
  setRouterLink(transition);
  console.log(JSON.stringify(Routerlink))
  transition.next()
})

router.start(App, 'app')

