<template>
   <header class="im-default-nav-bar" :class="navName">
        <div class="left-nav">
            <a class="content-left-nav" v-for="item in navLeft" v-link="item.link">
                <span v-if="item.text">{{item.text}}</span>
                <span v-if="item.slot">{{{item.slot}}}</span>
            </a>
        </div>
        <div class="right-nav">
            <a class="content-right-nav" v-for="item in navRight" v-link="item.link">
               <span v-if="item.text">{{item.text}}</span>
               <span v-if="item.slot">{{{item.slot}}}</span>
            </a>
        </div>
        <h1 id="nav-title">
            <a class="content-title-nav" v-for="item in navTitle" v-link="item.link">
                <span v-if="item.text">{{item.text}}</span>
                <span v-if="item.slot">{{{item.slot}}}</span>
            </a>
        </h1>
    </header>
</template>

<script>
export default {
    name: 'Header',
    props:{
        navTitle: {
            type: Array,
            default: function(){
            return []
            }
        },
        navLeft: {
            type: Array,
            default: function(){
                return []
            }
        },
        navRight: {
            type: Array,
            default: function(){
                return []
            }
        },
        navName: {
            type: String,
            default: ''
        }
    }
}
</script>

<style lang="less">

.im-default-nav-bar {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99999;
    background-color: #493e3c;
    height: 44px;
    line-height: 44px;
    color: white;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1);
    top: 0;
}

#nav-title {
    position: absolute;
    display: block;
    text-align: center;
    width: 100%;
    height: 100%;
    font-weight: 400;
    font-size: 18px;
}

.left-nav, .right-nav {
    position: absolute;
    left: 14px;
    height: 100%;
    background-color: transparent;
    border: none;
    text-align: left;
    z-index: 9999;
}

.content-left-nav, .content-right-nav, .content-title-nav {
    display: inline-block;
    overflow: hidden;
}

a span {
    color: white;
}

</style>



