<template>
<div class="page" transition="app">
	<Navbar :nav-title="activeNavbar.navTitle" :nav-left="activeNavbar.navLeft" :nav-right="activeNavbar.navRight" :nav-name="activeNavbar.name"></Navbar>
	<div class="content">
    <component :is="activeTabbar.name" keep-alive>
    </component>
	</div>
    <Tabbar :tabbars="validTabaars"></Tabbar>
</div>
</template>

<script>

import Tabbar from 'components/Tabbar.vue'
import Navbar from 'components/Navbar.vue'
import Home from './ButtonView.vue'
import Driving from './ActionSheetView.vue'
import Server from './ButtonView.vue'



export default { 
  name: 'IndexView',
  components: {
    Tabbar,
    Navbar,
    Home,
    Driving,
    Server
  },
  vuex: {
    getters: {
        activeTabbar: state => state.activeTabbar,
        currentDevice: state => state.currentDevice
    }
  },
  data(){
    return {
      //Tabbar
      tabbarsData: [
        { name: 'Server', text: '' },
        { name: 'Driving', text: '' },
        { name: 'Control', text: ''},
        { name: 'Home', text: ''},
        { name: 'Test', text: '' },
        { name: 'Myself', text: '' }
      ],
      //Navbar
      navbarsData: [
        { 
          name: 'Home',
          navTitle: [{
            'text': ''
          }],
          navLeft: [{
            'text':'pao',
            'link': {path: "button"}
          }]
        },
        {
          name: 'Driving',
          navTitle: [{
            'text': ''
          }],
          navLeft: [{
            'text':'hello',
            'link': 'actionsheet'
          },{
            'text':'11'
          }]
        },
        {
          name: 'Server',
          navTitle: [{
            'text': ''
          }],
          navLeft: [{
            'text':'world',
            'link': 'cell'
          }]
        }
      ]
    }
  },
  computed: {
    // Tabbar  navbar
    activeNavbar: function() {
      return this.navbarsData.filter( item => (item.name == this.activeTabbar.name) )[0] || {}
    },
    validTabaars: function() {
      return this.tabbarsData.filter( item => (item.name != (this.currentDevice === 'tbox' ? 'Server' : 'Control')) )
    }
  },
  route: {
    activate: function (transition) {
      console.log("add path => " + transition.to.path)
      transition.next()
    },
    deactivate: function (transition) {
      console.log('hook-index-view deactivate!')
      transition.next()
    },
    data: function(transition){
      console.log('hook-index-view data!')
      transition.next()
    }
  }

}

</script>

<style lang="less" scoped>

.content {
	top: 44px;
	position: absolute;
	width: 100%;
	bottom: 55px;
}
.page {
    background-attachment: fixed;
    background-image: url('./img/background.png');
    background-color: #252432;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
</style>



