import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  activeTabbar: { name: 'Home', text: ''},
  currentDevice: ''
}

const mutations = {
  SET_ACTIVE_NOTE (state, tabbar) {
    state.activeTabbar = tabbar
  }
}

export default new Vuex.Store({
  state,
  mutations
})


