<template>
  <div class="page actionSheet" style="overflow: hidden">
      <div class="hd">
          <h1 class="page_title">ActionSheet</h1>
      </div>
      <div @click="open">{{$route | json}} {{ "weui_actionsheet: " + actionSheetValue.message  + actionSheetValue.id }} 
        </div>
        <weui_actionsheet :items="items" :value.sync="actionSheetValue">
        </weui_actionsheet>
  </div>
</template>

<script>
import weui_actionsheet from 'components/ActionSheet'

export default {
  name: 'ActionSheetView',
  components: {
    weui_actionsheet
  },
  data(){
    return {
      items: [
        { message: 'Foo1', text: 'text2' },
        { message: 'hell',id: '333', text: 'text1'}
      ],
      actionSheetValue: {}
    }
  },
  created: function () {
  },
  methods: {
    open(){
      this.$broadcast('ActionSheetOpen');
    }
  }
}
</script>



