<template>
  <div id="actionSheet_wrap">
      <weui_mask :is-show.sync="isShow"></weui_mask>
      <div class="weui_actionsheet" :class="{weui_actionsheet_toggle:isShow}" id="weui_actionsheet" >
            <div class="weui_actionsheet_menu">
                <div class="weui_actionsheet_cell" v-for="item in items" @click="select(item)">{{ item.message }}</div>
            </div>
            <div class="weui_actionsheet_action">
                <div class="weui_actionsheet_cell" id="actionsheet_cancel" @click="close"></div>
            </div>
      </div>
  </div>
</template>

<script>
import weui_mask from 'components/Mask.vue'

export default {
  name:'weui_actionsheet',
  components: {
    weui_mask
  },
  props:{
    isShow:{
      type:Boolean,
      default:false
    },
    items: {
      type: Array,
      default: function () {
        return []
      }
    },
    value: {
      type: Object,
      default: function(){
        return {}
      }
    }
  },
  components: {
    weui_mask
  },
  events: {
    'ActionSheetOpen': 'open'
  },
  methods: {
    open(){
      this.isShow = true;
    },
    close(){
      this.isShow = false;
    },
    select(item){
      this.value = item;
      this.isShow = false;
    }
  }
}
</script>

<style lang="less">

@import "../common/style/widget/weui_tips/weui_actionsheet";

</style>


