<template>
  <div class="weui_mask_transition" :class="{weui_fade_toggle:isShow}" id="mask" :style="{display: isShow ? 'block' : 'none'}" v-on:click="close"></div>
</template>

<script>
export default {
  name:'weui_mask',
  props:{
    isShow:{
      type:Boolean,
      default:false
    }
  },
  methods:{
    close(){
      //TODO 
      this.isShow = false;
    },
    open(){
      this.isShow = true;
      //TODO 
    }
  }
}
</script>

<style lang="less">

@import "../common/style/widget/weui_tips/weui_mask";

</style>


