/**
 * Service for sending network requests.
 */

const CUSTOM_HEADERS = {'X-Requested-With': 'XMLHttpRequest'};
const COMMON_HEADERS = {'Accept': 'application/json, text/plain, */*'};
const JSON_CONTENT_TYPE = {'Content-Type': 'application/json;charset=utf-8'};

// import cors from './interceptor/cors';
// import body from './interceptor/body';
// import jsonp from './interceptor/jsonp';
// import before from './interceptor/before';
// import method from './interceptor/method';
// import header from './interceptor/header';
// import timeout from './interceptor/timeout';
// import Client from './client/index';
// import Request from './request';
// import Promise from '../promise';
import { assign, defaults, error, each, isPlainObject, isArray, isObject, isFunction } from './util';

export default function Fetch(url, options = {}) {

    var self = this || {};

    var timeout = options.timeout || 10000;

    var _options = defaults({}, options, Fetch.options);
    //content-type body 
    _options.body = formatBody(_options);

    var _fetch = fetch(url, _options);

    return Promise.race([_fetch,timer(timeout)])
              .then(checkStatus,function(res){
                alert(''+res)
              })
              .then(parseJSON);
  
}

//
function timer(t){
  return  new Promise((resolve, reject) => setTimeout( function(){
    reject('timeout')
  }, t))
}

Fetch.options = {
  headers: {
    'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8'
  }
};

Fetch.headers = {
    put: JSON_CONTENT_TYPE,
    post: JSON_CONTENT_TYPE,
    patch: JSON_CONTENT_TYPE,
    delete: JSON_CONTENT_TYPE,
    custom: CUSTOM_HEADERS,
    common: COMMON_HEADERS
};

function getContentTypeVal(request) {
  var contentTypeStr = request.headers['Content-type'];
  // var contentTypeStr = 'application/x-www-form-urlencoded';
  if( contentTypeStr.includes('application/x-www-form-urlencoded') ){
    return 0;
  } else if( contentTypeStr.includes('multipart/form-data') ) {
    return 1;
  } else if( contentTypeStr.includes('application/json') ) {
    return 2;
  } else if(contentTypeStr.includes('text/xml') ) {
    return 3;
  }

}

function checkStatus(response,a) {
  if (response.status >= 200 && response.status < 300) {
    return response
  } else {
    var error = new Error(response.statusText)
    error.response = response
    throw error
  }
}

function parseJSON(response) {
  return response.json()
}

function formatBody(request) {
  // var contentTypeVal = getContentTypeVal(request);
  // switch(contentTypeVal){
  //   case 0:
  //     params = formatBodyToString(request.body);
  //     break;
  //   case 1:
  //     break;
  //   case 2:
  //     params = JSON.stringify(request.body);
  //     break;
  //   case 3:
  //     break;
  // }
  var body = request.body;
  var params = formatBodyToString(body);
  return params
} 

function formatBodyToString (obj) {

    var params = [], escape = encodeURIComponent;

    params.add = function (key, value) {

        if (isFunction(value)) {
            value = value();
        }

        if (value === null) {
            value = '';
        }

        this.push(escape(key) + '=' + escape(value));
    };

    serialize(params, obj);

    return params.join('&').replace(/%20/g, '+');
};

function serialize(params, obj, scope) {

    var array = isArray(obj), plain = isPlainObject(obj), hash;

    each(obj, (value, key) => {

        hash = isObject(value) || isArray(value);

        if (scope) {
            key = scope + '[' + (plain || hash ? key : '') + ']';
        }

        if (!scope && array) {
            params.add(value.name, value.value);
        } else if (hash) {
            serialize(params, value, key);
        } else {
            params.add(key, value);
        }
    });
}



